
import Vue from "vue"
export default {
    namespaced:true,
    state:{
        cartData:[],
        allSelected:false,
    },
    mutations:{
        getCartData(state,{cartData}){
            //state
            cartData.forEach(ele=>{
                ele.selected = false;
            })
            state.cartData = cartData
        },
        changeCartData(state,{num,index}){
            //
            if(num){
                state.cartData[index].number = num
            }else{
                state.cartData.splice(index,1)
            }
        },
        changeSelected(state,{type,index}){
            // 
            if(type==="all"){
                //
                state.allSelected = !state.allSelected;
                state.cartData.forEach(ele=>{
                    ele.selected = state.allSelected 
                })
            }else{
                console.log(type)
                //
                 state.cartData[index].selected = !state.cartData[index].selected;
                 //
                 var all = true;//true
                 state.cartData.forEach(ele=>{
                    if(!ele.selected){ // false
                        all = false
                    }
                 });
                 state.allSelected = all; //
            }
        }
    },
    // vuex 
    getters:{
        cartData(state){
            return state.cartData
        },
        allSelected(state){
            return state.allSelected
        }
    },
    actions:{
        getCartData({commit}){
            //
           
            Vue.http.jsonp("http://datainfo.duapp.com/shopdata/getCar.php?userID=tom666")
            .then(res=>{
                //actions ==mutations
                commit("getCartData",{cartData:res.data})

            })
        },
        changeCartData({commit},{index,num,id}){
            //
            let url = `http://datainfo.duapp.com/shopdata/updatecar.php?userID=tom666&goodsID=${id}&number=${num}`
            Vue.http.get(url).then(res=>{
                //commit
                commit("changeCartData",{index,num})
            })
        },
        changeSelected({commit},{type,index}){
            //mutation
            commit("changeSelected",{type,index})
        }
    }
}


